import ServicesComponent from './services-component';

const Services = () => (
    <div className="row p-5 bg-white">
        <div className="col">
            <div className="row justify-content-center">
                <div className="col-12 text-center">
                    <p className="text-dark">What can we do?</p>
                    <h2 className="font-weight-bold">OUR SERVICES</h2>
                </div>
            </div>
            <div className="row justify-content-center">
                <ServicesComponent compTitle="LABS" compStyle="" compImg="/static/creative.svg">
                    <p className="card-text mb-2 font-weight-light">Research</p>
                    <p className="card-text mb-2 font-weight-light">Development</p>
                    <p className="card-text mb-2 font-weight-light">Technical Guidance</p>
                </ServicesComponent>

                <ServicesComponent compTitle="ADVISORY" compStyle="" compImg="/static/profits.svg">
                    <p className="card-text mb-2 font-weight-light">Blockchain Consulting</p>
                    <p className="card-text mb-2 font-weight-light">ICO Launchpad</p>
                </ServicesComponent>

                <ServicesComponent compTitle="CAPITAL" compStyle="" compImg="/static/investment.svg">
                    <p className="card-text mb-2 font-weight-light">Fund</p>
                    <p className="card-text mb-2 font-weight-light">Investment Access</p>
                    <p className="card-text mb-2 font-weight-light">Portfolio</p>
                </ServicesComponent>
            </div>
        </div>
    </div>
)

export default Services


