import Zoom from 'react-reveal/Zoom';

const cardHeight = {
    height: 320,
    boxShadow: '0 4px 8px 0 rgba(0,0,0,0.2)',
    transition: '0.3s'
}

const cardImageStyle = {
    height: 80
}

const buttonStyle = {
    color: '#49946B',
    border: '1px solid #49946B',
    width: '100%'
}

const ServiceComponent = ({ compImg, compTitle, children, compStyle }) => (
    <Zoom bottom duration={1500}>
        <div className={`col-lg-4 col-xl-3 col-md-6 col-sm-8 p-3 ${compStyle}`}>
            <div className="card text-center p-3" style={cardHeight}>
                <img className="card-img-top" src={compImg} alt="Card image cap" style={cardImageStyle}></img>
                <div className="card-body">
                    <h5 className="card-title font-weight-bold">{compTitle}</h5>
                    {children}
                </div>
                <div>
                    <a href="#" className="btn btn-outline" style={buttonStyle}>LEARN MORE</a>
                </div>
            </div>
        </div>
    </Zoom>
)

export default ServiceComponent


