import Link from 'next/link'

const linkStyle = {
    marginRight: 15
}

const layoutStyle = {
    height: 80
}

const bgLogo = {
    backgroundImage: 'url("/static/logo.png")',
    backgroundSize: 'contain',
    backgroundPosition: 'center'
}

const Header = () => (
    <div className="row bg-white" style={layoutStyle}>
        <div className="col-md-4 col-lg-3 col-sm-12 text-center">
            <img style={layoutStyle} src="/static/logo.png"></img>
        </div>
        {/* <div className="col-md-8 col-12">
            <div className="align-items-center row h-100">
                <div className="col">
                <div className="justify-content-end row pr-5">
                    <Link href="/">
                        <a style={linkStyle}>Home</a>
                    </Link>
                    <Link href="">
                        <a style={linkStyle}>Contact</a>
                    </Link>
                    </div>
                </div>
            </div>
        </div> */}
    </div>
)

export default Header


