import fetch from 'isomorphic-unfetch'
import React from 'react'
import Link from 'next/link'

const cardHeight = {
    height: 320
}

const cardImageContainerHeight = {
    minHeight: 200
}

const cardImageStyle = {
    minHeight: 200
}

const Blogs = (data) => (
    <div className="row">
        <div className="col">
            <div className="row justify-content-center">
                <div className="col-12 text-center mt-5">
                    <h2>Blogs</h2>
                </div>
            </div>
            <div className="row justify-content-center">
                {data.data.items.map(({ title, thumbnail, link, guid }) => (
                    <div className="col-md-5 col-11 p-3" key={guid.toString()}>
                        <a href={link}>
                            <div className="card text-center p-3" style={cardHeight}>
                                <div className="row h-50">
                                    <img className="card-img-top h-100" src={thumbnail} alt="Card image cap"></img>
                                </div>
                                <div className="card-body h-50 row align-items-end">
                                    <h5 className="card-title font-weight-bold">{title}</h5>
                                </div>
                            </div>
                        </a>
                    </div>
                ))}
            </div>
        </div>
    </div>
)

export default Blogs



