import WorksComponent from "./works-component";

const bgImg = {
    backgroundImage: 'url("/static/work.jpeg")',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    position: 'relative'
}

const bgMask = {
    backgroundColor: '#333',
    opacity: 0.5,
    position: 'absolute',
    width: '100%',
    height: '100%',
    zIndex: 0,
    top: 0,
    left: 0
}

const container = {
    zIndex: 1
}

const Works = () => (
    <div className="row p-5 bg-white" style={bgImg}>
        <div className="col" style={container}>
            <div className="row justify-content-center mb-5">
                <div className="col-12 text-center text-white">
                    <h2>WHERE DO WE STAND NOW?</h2>
                </div>
            </div>
            <div className="row justify-content-center">
                <WorksComponent count="5" compInnerTitle="projects" compTitle="Capital" compStyle="mr-lg-auto" />
                <WorksComponent count="4" compInnerTitle="locations" compTitle="Team Distribution" compStyle="mr-lg-auto" />
                <WorksComponent count="1" compInnerTitle="ICO's" compTitle="Advisory" compStyle="" />
            </div>
        </div>
        <div style={bgMask}>
        </div>
    </div>
)

export default Works


