import Slide from 'react-reveal/Slide';

const mainContainer = {
    backgroundImage: 'url("/static/about.jpg")',
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    position: 'relative'
}

const titleStyles = {
    color: '#FFE',
    fontSize: '50px',
    marginTop: 100
}

const subTitleStyle = {
    color: '#0D92D7'
}

const description = {
    color: '#FFFEEE'
}

const buttonStyle = {
    backgroundColor: '#25AE5E',
    color: '#FFE'
}

const logoStyle = {
    width: 30
}

const bgMask = {
    backgroundColor: '#333',
    opacity: 0.9,
    position: 'absolute',
    width: '100%',
    height: '100%',
    zIndex: 0,
    top: 0
}

const container = {
    zIndex: 1
}


const About = () => (
    <div className="row p-md-5 bg-white justify-content-center" style={mainContainer}>
        <div className="col-12 col-xl-9" style={container}>
            <div className="row justify-content-center mb-5" style={titleStyles}>
                <Slide left cascade>
                    <span className="mr-md-4 font-weight-bold">RESEARCH.</span>
                </Slide>
                <Slide left cascade>
                    <span className="mr-md-4 font-weight-bold">ADVISORY.</span>
                </Slide>
                <Slide left cascade>
                    <span className="font-weight-bold">CAPITAL.</span>
                </Slide>
            </div>
            <div className="row justify-content-center mb-5">
                <div className="col-11 col-lg-8">
                    <Slide bottom cascade>

                        <h3 className="font-weight-bold" style={subTitleStyle}>
                            ... ON BLOCKCHAIN AND DIGITAL ASSETS
                    </h3>
                    </Slide>
                    <Slide bottom cascade>
                        <p style={description} className="font-weight-light">
                            Bloqwise is a Research and Advisory firm focused on blockchain technology and digital assets. We are commited to support the sustainability and accelarate the advancement of blockchain technology by leveraging our global network and expertise.
                        </p>
                        <div className="row justify-content-left">
                            <div className="col-auto">

                                <a href="#"><img src="/static/telegram (1).svg" alt="Telegram" className="text-white mr-4" style={logoStyle}></img></a>
                                <a href="#"><img src="/static/medium (2).svg" alt="Medium" className="text-white mr-4" style={logoStyle}></img></a>
                                <a href="#"><img src="/static/twitter.svg" alt="Twitter" className="text-white mr-5" style={logoStyle}></img></a>
                                <button className="btn" style={buttonStyle}>CONTACT US</button>
                            </div>
                        </div>
                    </Slide>
                </div>
            </div>
        </div>
        <div style={bgMask}>
        </div>
    </div >
)

export default About


