import Zoom from 'react-reveal/Zoom';
const componentStyle = {
    height: 120,
    width: 120,
    border: '2px solid #FFF',
    borderRadius: '50%',
    color: '#000',
    padding: 3,
    backgroundClip: 'content-box', // support: IE9+
    backgroundColor: '#FFF'

}


const titleStyle = {
    color: '#FFF'
}

const WorksComponent = ({ count, compInnerTitle, compTitle, compStyle }) => (
    <Zoom top duration={1500}>
        <div className={`col-lg-3 col-md-6 col-sm-8 p-3 ${compStyle}`}>
            <div className="row justify-content-center">
                <div className="text-center bg-white align-items-center" style={componentStyle}>
                    <h2 className="m-0 pt-4">{count}</h2>
                    <p className="text-muted font-weight-normal">{compInnerTitle}</p>
                </div>
            </div>
            <div className="row justify-content-center mt-3" style={titleStyle}>
                <h5 className="font-weight-bold">{compTitle}</h5>
            </div>
        </div>
    </Zoom>
)

export default WorksComponent


